@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .glass-slate {
    @apply bg-slate-800 backdrop-filter backdrop-blur-xl shadow-lg bg-opacity-25;
  }

  .strong-slate {
    @apply bg-slate-800 backdrop-filter backdrop-blur-3xl shadow-lg bg-opacity-95;
  }

  .glass-amber {
    @apply bg-amber-800 backdrop-filter backdrop-blur-lg shadow-lg bg-opacity-50;
  }

  .glass-lightblue {
    @apply bg-lightblue-100 backdrop-filter backdrop-blur-lg shadow-lg bg-opacity-50;
  }

  .transparent-gradient {
    @apply bg-gradient-to-b from-lightblue-100 to-transparent;
  }

  .mp5 {
    @apply m-5 p-5;
  }

  .mp3 {
    @apply m-3 p-3;
  }

  .t200e {
    @apply transition-all duration-200 ease-in-out;
  }

  .t500e {
    @apply transition-all duration-500 ease-in-out;
  }

  .t1000e {
    @apply transition-all duration-1000 ease-in-out;
  }

  .t100d200e {
    @apply transition-all duration-100 delay-200 ease-in-out;
  }

  .arrow-icon-pop-left {
    @apply group-hover:opacity-100 -translate-x-3 group-hover:translate-x-0;
  }

  .arrow-icon-pop-right {
    @apply group-hover:opacity-100 translate-x-3 group-hover:translate-x-0;
  }

  .nudgeup {
    @apply hover:-translate-y-2;
  }

  .zoomout {
    @apply hover:scale-75;
  }

  .zoomin {
    @apply hover:scale-125;
  }

  .zoomin-sm {
    @apply hover:scale-110;
  }

  .point {
    @apply hover:cursor-pointer;
  }

  .btn {
    @apply mt-6 px-6 py-3 bg-slate-800 text-white font-extrabold rounded-xl hover:bg-slate-900 t200e;
  }
  .btn-lt {
    @apply mt-6 px-6 py-3 bg-slate-600 text-white font-extrabold rounded-xl hover:bg-slate-800 t200e;
  }

  .btn-lightblue-200 {
    @apply px-6 py-3 border-2 bg-lightblue-200 text-darkslategrey font-extrabold rounded-xl hover:bg-lightblue-500 hover:text-white t200e;
  }

  .fadein80 {
    @apply opacity-80 hover:opacity-100;
  }

  .fadein0 {
    @apply opaciy-0 hover:opacity-100;
  }

  .btn-lightblue {
    @apply px-6 py-3 border-blue-100 border-4 font-medium rounded-2xl text-darkslategrey bg-white hover:bg-lightblue-100;
  }

  .btn-yellow {
    @apply px-6 py-3 border-white border-4 font-medium rounded-2xl text-darkslategrey bg-yellow-300;
  }

  .fact-yellow {
    @apply px-6 py-3 font-medium rounded-full text-darkslategrey bg-yellow-300;
  }

  .title {
    @apply text-5xl lg:text-7xl font-bold;
  }
  .subtitle {
    @apply text-5xl font-bold;
  }
  .paragraph {
    @apply text-2xl font-light;
  }
  .vl {
    @apply h-96 border-4 m-12;
  }
  .vl-sm {
    @apply h-72 border-4 m-8;
  }
  .hl {
    @apply w-full border-4 m-12;
  }

  .notOpen {
    @apply cursor-not-allowed invert-25;
  }

  .isOpen {
    @apply cursor-pointer;
  }

  .forms {
    @apply bg-lightblue-400  p-3 rounded-md border-darkslateblue border-2  text-darkslategrey;
  }
  .inner-forms {
    @apply bg-gray-200 p-3 rounded-md border-darkslateblue  border-2  text-darkslategrey;
  }

  .newtab {
    @apply w-6 h-6;
  }
  .newtab-sm {
    @apply w-4 h-4;
  }
  .newtab-xs {
    @apply w-3 h-3;
  }
}
